body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.LandingPage-container {
    background: #0288D1;
    text-align: center;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.LandingPage-container h1 {
    font-weight: bold;
}
.LandingPage-container span {
    font-size: 25px;
    font-weight: 300;
}
.LandingPage-container .lp-btn {
    margin: 0 auto;
    margin-top: 30px;
    padding: 10px 20px;
    border: 6px solid white;
    max-height: 400px;
    color: white;
    font-weight: bold;
    transition: 0.5s;
}
.LandingPage-container .lp-btn:hover {
    background: white;
    color: #1976D2;
}


.NavBar-container nav {
    position: fixed;
    color:white;
    font-size: 1.25em;
    background: #0288D1;
}
.NavBar-container .nav-link, .navbar-brand {
    color:white !important;
    font-weight: 300;
}
.NavBar-container .nav-link:hover {
    color: grey !important;
}
.resume {
    position: absolute;
    left:0;
    bottom:10;
    
}
.About-container {
    text-align: left;
    margin: 30px 0px;
    position: relative;
}
.About-container h1 {
    text-align: left;
}
.About-container section {
    width: 100%;
}
.About-container section p {
    text-align: left;
}
.About-container .content {
    padding-bottom: 40px;
}
.About-container .image-left {
    display:flex;
    justify-content: center;
    align-items: center;
}
.About-container img {
    width: 50%;
    border-radius: 60%;
    -webkit-animation:spin 7s linear infinite;
    animation:spin 7s linear infinite;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.Projects-container {
    background: #0288D1;
    padding-bottom: 30px;
}
.Projects-container h1 {
    text-align: center;
    color:white;
}
.Projects-container .row .col-sm-12 .card {
    min-height: 280px;
    margin: 0 auto;
    text-align: center;
    border: 0.5px solid lightgrey;
    margin-top: 20px;
}

.Contact-container {
    text-align: center;
    background: #0288D1;
}
.Contact-container h2 {
    text-align: center;
    color:white;
}
.Contact-container i {
    margin:20px;
}
/*Global*/
* {
    margin: 0;
    padding: 0;
}
body {
    font-weight: 400;
    font-family: 'Quicksand', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
h1, h2, h3, h4, h5, h6 {
    margin-top: 30px;
    font-weight: 400;
}
.btn {
    background: #536DFE !important;
    border-color: #536DFE !important;
}
.fab , .fas {
    color: white !important;
}
p {
    color:#757575;
}
a:focus{
    outline: none;   
}
